//Goggle Map

function initialize() {

			/*
				http://openapi.map.naver.com/api/geocode.php?key=f32441ebcd3cc9de474f8081df1e54e3&encoding=euc-kr&coord=LatLng&query=1
				
				https://www.google.co.kr/maps/place/%EC%84%9C%EC%9A%B8%ED%8A%B9%EB%B3%84%EC%8B%9C%EC%B2%AD/@37.5651027,126.9781819,16z/
				data=!4m5!1m2!2m1!1z7ISc7Jq47Iuc7LKt!3m1!1s0x357ca2f332cb082b:0xe92b70ac420cf0a8

				     x,y   .
				
				  : @37.571861,126.985028
			*/
			var Y_point			= 37.571861;		// Y 
			var X_point			= 126.985028;		// X 

			var zoomLevel		= 15;						//      

			var markerTitle		= " ";				//       
			var markerMaxWidth	= 300;						//      

			//  
			var contentString	= '<div id="content">' +
					'<div id="siteNotice">' +'</div>'+	
					'<div id="bodyContent">'+ 
						'<p> !<br /><a href="http://media.co.kr" target="_blank">http://media.co.kr</a></p>'+	
					'</div>'+ 
				'</div>';

			var myLatlng = new google.maps.LatLng(Y_point, X_point);
			var mapOptions = {
								zoom: zoomLevel,
								center: myLatlng,
								mapTypeId: google.maps.MapTypeId.ROADMAP
			}
			var map = new google.maps.Map(document.getElementById('map_canvas'), mapOptions);

			var marker = new google.maps.Marker({
													position: myLatlng,
													map: map,
													title: markerTitle
			});

			var infowindow = new google.maps.InfoWindow(
														{
															content: contentString,
															maxWidth: markerMaxWidth
														}
			);

			google.maps.event.addListener(marker, 'click', function() {
				infowindow.open(map, marker);
			});
		}